export default {
	data() {
		return {
			isCanUse:true
		};
	},
	methods: {
		// 用户点击授权
		wxGetUserInfo() {
			uni.login({
				success(res) {
					let code = res.code
					//获取用户信息
					uni.getUserInfo({
						success(data) {
							// console.log(data);
							uni.request({
									url: "https://applet.51tiaoyin.com/public/applet/login/",
									method: "GET",
									dataType:JSON,
									data: {
										"code": code,
										"iv": data.iv,
										"encryptedData": data.encryptedData
									},
									success(res) {
										// JSON.stringify(res.data.wechat_name);
										// console.log(res);
										console.log(JSON.parse(res.data));
										const data = JSON.parse(res.data);
										console.log(data.data.uid);
										if(data.code == 200){
											uni.showToast({
												title: "登录成功"
											});
											// uni.reLaunch({
											// 	url:"../index/index",
											// });
											// 把用户信息写入缓存
											
											// console.log(res.data.data);
											uni.setStorageSync('user_name', data.data.wechat_name);
											uni.setStorageSync('user_avatar', data.data.wechat_img);
											uni.setStorageSync('user_uid', data.data.uid);
										}
										
									},
									fail(res) {
										uni.showToast({
											title: '获取授权信息失败',
											icon: 'none'
										});
										console.log(res);
									}
								})
						}
					})
				}
			})
		},
		//获取用户缓存信息
		getinfo(){
			console.log("haha");
			try {
			    const value = uni.getStorageSync('user_uid');
			    if (value) {
			        console.log(value);
			    }else{console.log("value不存在");}
			} catch (e) {
			    // error
				console.log(e);
			}
		},
		},
	onLoad() {
		
	}
}
